<template>
<div>
    <div class="cell-info">
        <div class="cell-info-left">
            <div class="img-container">
                <img v-lazy="cellData.headimgurl" class="img" />
            </div>
        </div>
        <div class="cell-info-middle">
            <span class="name">{{cellData.username}}</span>
        </div>
        <div class="right-abslute">
            <span class="icon-phone iconfont"></span>
            <span class="under-phone">{{cellData.phone||""}}</span>
        </div>
    </div>
</div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "bind-tjt-cell",
  data() {
    return {};
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          headimgurl: "http://www.jubenxiong.com/aml/img/nx_1515661463680.png",
          phone: 4001368868,
          username: "L",
          userid:"",
          "role":"4"
        };
      }
    }
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.cell-info {
    width: 100%;
    height 56px;
    font-size: 0px;
    display: flex;
    align-items: center;
    text-align center;

    .cell-info-left {
        flex:0 0 60px;
        text-align: center;
        .img-container{
            display inline-block;
            width 40px;
            height 40px;
            .img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position relative;
            }
        }
    }

    .cell-info-middle {
        flex: 1;
        text-align: left;
        .name {
            max-width 120px;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space:nowrap;
            display inline-block;
            padding-top 3px;
            font-size: 17px;
            font-weight: 700;
            color #42495C;
            
        }
    }

    .right-abslute {
        text-align: left;
        flex 0 0 115px;
        .icon-phone{
            font-size: 16px;
            color: #878a94;
        }
        .under-phone {
            color: #aaa;
            font-size: 15px;
        }
    }
}


</style>



