<template>
    <div class="agency-shops">
        <my-search ref="mysearch" :tableclass="'shop-width'" class="my-search" @my-search-focus="onScearchFocus">
        </my-search>
        <keep-alive>
            <router-view @search-panel-leave="onSearchPanelLeave"></router-view>
        </keep-alive>
        <mt-popup class="select-popup" position="bottom" popup-transition="popup-slide" :modal=true v-model="showPopup">
            <div ref="betterscroll" class="bind-tjt-scroller">
               <div class="content">
                  <p class="select-word">{{roleName}}</p>
                  <bind-tjt-cell v-for="cellData in bindPopData" :cell-data="cellData" class="bind-tjt-cell" @touchstart.native="onCellDown" @touchend.native="onCellUp" @click.native="cellClickHandle(cellData,$event)"></bind-tjt-cell>
               </div>
            </div>
        </mt-popup>
    </div>
</template>

<script>
import mySearch from "../../../public/mySearch.vue";
import bindTjtCell from "../../../public/bindTJTCell.vue";
import { mapState } from "vuex";
import Vue from "vue";
import { Toast ,Picker} from "mint-ui";
import "mint-ui/lib/toast/style.css";
import "mint-ui/lib/picker/style.css";
Vue.component(Toast.name, Toast);
Vue.component(Picker.name, Picker);
import bus from '../../../../common/js/eventBus.js';
import BScroll from "better-scroll";
import {MessageBox} from "../../../popupBox/messageBox/message-box.js"
Vue.component(MessageBox.name, MessageBox);

export default {
  name: "agency-shops",
  data() {
    return {
        cellData:[],
        showPopup:false,
        bindShopData:null,
        bindUpperData:null,
        cancelBindShopData:null,
        bindPopData:[],
        curPage:0,
        bindRole:""      //34
    };
  },
  components: {
    mySearch,
    bindTjtCell
  },
  created() {
  },
  beforeRouteLeave(to, from, next){  
    MessageBox.close();
    if(this.showPopup) this.hideBindPopUp();
    bus.$off("shop-bind-call",this.onShopBindCall);
    bus.$off("shop-bind-cancel-call",this.onShopBindCancelCall);
    next();
  },
  mounted(){
    console.log("agencyShopsmounted..");
    bus.$on("shop-bind-call",this.onShopBindCall);
    bus.$on("shop-bind-cancel-call",this.onShopBindCancelCall);
  },
  computed: {
    ...mapState(["openid", "path","userid","agencyTJTArr","agencySalesmanArr"]),
    roleName(){
      if(this.bindRole=="3") return "";
      else return ""
    }
  },
  methods: {
    onSearchPanelLeave(){
      this.$refs.mysearch.initiativeBlur();
    },
    onScearchFocus(){
      if(this.$route.name!="agencyShopsList") return;
      this.$router.push({name:"agencyShopsSearch"});
    },
    onShopBindCall(data,bindRole){
      this.bindShopData=data;
      this.bindRole=bindRole;
      if(bindRole=="4"){   //
        if(this.agencyTJTArr&&this.agencyTJTArr.length!=0){
          this.bindPopData=this.agencyTJTArr;
          this.showBindPopUp();
        } 
        else this.requestBindRolesData();
      }else if(bindRole=="3"){
        if(this.agencySalesmanArr&&this.agencySalesmanArr.length!=0){
          this.bindPopData=this.agencySalesmanArr;
          this.showBindPopUp();
        } 
        else this.requestBindRolesData();
      }
    },
    hideBindPopUp(){
      if(this.betterscroll){
        this.betterscroll.destroy();
        this.betterscroll=null;
      }
      this.showPopup=false;
    },
    showBindPopUp(){
      this.showPopup=true;
      this.$nextTick(() => {
          console.log("this.$refs.betterscroll="+this.$refs.betterscroll);
          this.betterscroll = new BScroll(this.$refs.betterscroll, {click: true});
      });
    },
    requestBindRolesData(){
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + "php/Route.php", {
        type:'showagentlist',
        aid:this.userid,
        roletp:this.bindRole,
        page:0
      })
      .then(response => {
        let resultData = response.body.arr;
        if(!resultData||resultData.length==0){
          Toast({message: ""+this.roleName,iconClass: "iconfont icon-tishi1",duration: 1500});
        }
        else{
          if(this.bindRole=="4") this.$store.commit("UPDATA_A_TJT", resultData);
          else this.$store.commit("UPDATA_A_SALEMAN", resultData);
          this.bindPopData=resultData;
          this.showBindPopUp();
        }
        this.$store.dispatch("toggleLoading", false);
      })
      .catch(response=>{
        //
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
    },
    onCellUp(e) {
      let cell = e.currentTarget;
      cell.style.backgroundColor = "#FFFFFF";
    },
    onCellDown(e) {
      let cell = e.currentTarget;
      cell.style.backgroundColor = "#eee";
    },
    cellClickHandle(cellData,event){
      //
      // if (event._constructed) return; // betterscrollerclick
      this.hideBindPopUp();
      this.bindUpperData=cellData;
      var spanLeft1='<span style="font-weight:700;color:#42495C;">';
      var spanLeft2='<span style="font-weight:700;color:#D2B48C;">';
      var spanRight='</span>';  
      MessageBox.confirm('',{title: '',
        message: ""+spanLeft1+"\'"+this.bindShopData.username+"\'"+spanRight+""+this.roleName+spanLeft2+"\'"+cellData.username+"\'"+spanRight+"?",
        showCancelButton: true}).
        then(action => {
          this.requestBindRelationship();
        }).catch(response=>{
          //
          console.log(response);
        });
    },
    requestBindRelationship(){       //
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + "php/Route.php", {
        type:'bthconn',
        bid:this.bindShopData.userid,
        hid:this.bindUpperData.userid,
        aid:this.userid,
        role:this.bindRole
      })
      .then(response => {
        let resultData = response.body;
        if(resultData.error=="ok"){
          bus.$emit("updata-bind-data",this.bindShopData,this.bindUpperData,this.bindRole);
          Toast({message: "",iconClass: "iconfont icon-tishi1",duration: 1500});
        }else{
          Toast({message: "",iconClass: "iconfont icon-tishi1",duration: 1500});
        }
        this.$store.dispatch("toggleLoading", false);
      })
      .catch(response=>{
        //
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
    },
    onShopBindCancelCall(cancelBindShopData,bindRole){
      this.cancelBindShopData=cancelBindShopData;
      this.bindRole=bindRole;
      let upperName=bindRole=="3"?cancelBindShopData.salemanName:cancelBindShopData.tjtName;
      var spanLeft1='<span style="font-weight:700;color:#42495C;">';
      var spanLeft2='<span style="font-weight:700;color:#D2B48C;">';
      var spanRight='</span>';  
      MessageBox.confirm('',{title: '',
        message: ""+spanLeft1+"\'"+cancelBindShopData.username+"\'"+spanRight+""+this.roleName+spanLeft2+"\'"+upperName+"\'"+spanRight+"?",
        showCancelButton: true}).
        then(action => {
          this.requestBindCancel();
        }).catch(response=>{
          //
          console.log(response);
        });
    },
    requestBindCancel(){
      this.$store.dispatch("toggleLoading", true);
      let upperId=this.bindRole.toString()=="3"?this.cancelBindShopData.salemanId:this.cancelBindShopData.tjtId;
      this.$http
      .post(this.path + "php/Route.php", {
        type:'bossec',
        aid:this.userid,
        bid:this.cancelBindShopData.userid,
        sid:upperId
      })
      .then(response => {
        this.$store.dispatch("toggleLoading", false);
        let resultData = response.body;
        if(resultData.error=="ok"){
          bus.$emit("updata-cancel-bind-data",this.cancelBindShopData,this.bindRole);
          Toast({message: "",iconClass: "iconfont icon-tishi1",duration: 1500});
        }else{
          Toast({message: "",iconClass: "iconfont icon-tishi1",duration: 1500});
        }
      })
      .catch(response=>{
        //
        this.$store.dispatch("toggleLoading", false);
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.agency-shops {
  height 100%; 
  box-sizing: border-box;
  padding-top:.3rem;
  display flex;
  flex-direction column;
  flex 1;

  .my-search {
    position relative;
    z-index 1;
    box-sizing border-box;
    width: 100%;
    background-color #e5e5e5;
    box-shadow: 0 1px 1px rgba(80, 80, 80, 0.25);
    .icon-chakan {
      color: #42495C;
      font-size: 20px;
      font-weight 700;
    }
  }

  .select-popup{
    width 100%;
    position relative;
    height 38vh;
    border-top 14px solid #42495C;
    border-radius: 7px 7px 0px 0px;

    .bind-tjt-scroller{
      height 100%;
      position absolute;
      left 10px;
      right 10px;
      overflow: hidden;

      .content{
        width 100%;
        border-bottom 1px solid #dedede;

        .select-word{
          width 100%;
          color:#333;
          font-size 14px;
          line-height 36px;
          padding-left 10px;
          position relative;
          top 4px;
        }

        .bind-tjt-cell{
          width 100%;
          border-top 1px solid #dedede;
          background-color: #fff;
          // box-sizing:content-box;
        }
      }
      
    }
  }
}

</style>



